import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../../global/global';
import { CustomValidators } from '../../../../../../utilities/customValidators';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond, CustomValidators],
  selector: 'app-add-package-name',
  templateUrl: './add-package-name.component.html',
  styleUrls: ['./add-package-name.component.scss']
})
export class AddPackageNameComponent implements OnInit {
  addPackageName: FormGroup
  loginData: any;
  proData: any;
  submitted: boolean;
  mailError: boolean;
  disableSignIn: boolean;
  countData: any;
  response: any;
  count1: boolean;
  emailID: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    private formBuilder: FormBuilder,
    public toastr: ToastrService,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.beyond.beyondAdmin()
    this.countAccountType()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.emailID = this.loginData.emailId
    this.addPackageName = this.formBuilder.group({
      pbPackageName: ['', Validators.required],
      pbExpectedNetProfit: ['', Validators.required],
      colorCode: ['', Validators.required],

    })

  }
  get f() { return this.addPackageName.controls; }
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.addPackageName.invalid) {
      return;
    }

  }
  //Counting The data start here
  countAccountType() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/accountTypeCount/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var equipAgreement = data.json();
        if (equipAgreement.status == 200) {
          this.countData = equipAgreement.response;

          console.log("countData" + JSON.stringify(this.countData[0].count))
        } else {

        }
      }, error => {
      })
  }
  //Counting The data End here
  savePBPackageName() {
    console.log("count data" + this.countData[0].count)
    if (this.countData[0].count < 3) {
      console.log("Count Data Value" + JSON.stringify(this.countData))
      let obj = {
        "proId": this.loginData.loginCode,
        "packageName": this.addPackageName.value.pbPackageName,
        "exceptedNetProfit": this.addPackageName.value.pbExpectedNetProfit,
        "colorCode": this.addPackageName.value.colorCode,
        "createdBy": this.emailID,
        "status": "ACTIVE"
      }
      console.log("obj" + JSON.stringify(obj))
      this.http.post(Global.url + '/addPBPackageName', obj)
        .subscribe(data => {
          var groupData = data.json();
          this.count1 = false;
          console.log("groupData" + JSON.stringify(groupData))
          this.toastr.success('Package Name Added Successfuly...');
          this.router.navigate(['/accountSettings/accountSettings/4/3'])
        }, error => {
          console.log(error);// Error getting the data
        });

    }
    else {
      this.count1 = true;
      this.toastr.warning('Already 3 records are existed...');
      // return false
    }


    // this.router.navigate(['/proz/accountSettings'])  
  }
  accVerification() {
    console.log("teja" + JSON.stringify(this.addPackageName.value.pbPackageName))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.addPackageName.value.pbPackageName == "") {
      this.toastr.warning("markUp Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkpackageName/' + this.addPackageName.value.pbPackageName + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  cancelPBPackageName() {
    this.router.navigate(['/accountSettings/accountSettings/4/3'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
