import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-inventory-subcategory',
  templateUrl: './edit-inventory-subcategory.component.html',
  styleUrls: ['./edit-inventory-subcategory.component.scss']
})
export class EditInventorySubcategoryComponent implements OnInit {
  editInventory: FormGroup
  loginData: any;
  proData: any;
  mailError: boolean;
  disableSignIn: boolean;
  emailId: any;
  invImg: any;

  activeTab: Number = 0;
  percentage: number;
  progressShow: boolean;
  progressHide: boolean;
  img1: boolean;
  file2Upload: string | Blob;
  imagePreview: string = "assets/img/no-image.png";
  img: boolean;
  invData: boolean;
  invantoryCat: any = [];
  submitted: boolean;
  categoryDet: any;
  IdValue: any;
  PbSubCategories: any = [];
  id: any;
  mailResponse: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.id = this.route.snapshot.params['id']
    console.log("id" + this.id)
    this.proData = this.loginData.loginCode,
      this.listCategories()
    this.viewSubCategorie()
    this.emailId = this.loginData.emailId,
      this.editInventory = this.formBuilder.group({
        category: ['', Validators.required],
        subCategory: ['', Validators.required],
        picture: ['', Validators.required],
      })
  }


  onSubmit() {
    this.submitted = true;
    console.log("this.editInventory.valid" + this.editInventory.valid)
    console.log("this.editInventory.invalid" + this.editInventory.invalid)
    if (this.editInventory.value.category != undefined && this.editInventory.value.subCategory != '') {
      this.updateInvSubCategories()
      return;
    }
    else {

    }

  }
  get f() { return this.editInventory.controls; }
  updateInvSubCategories() {
    console.log("picture" + Global.documentName)
    console.log("category" + this.editInventory.value.category)
    console.log("subCategory" + this.editInventory.value.subCategory)
    if (Global.documentName != '') {
      console.log("if block is calling")
      let obj = {
        "loginData": this.proData,
        "emailId": this.emailId,
        "id": this.id,
        "category": this.editInventory.value.category,
        "subCategory": this.editInventory.value.subCategory,
        "inventoryImage": '' + Global.documentName,
        "createdOn": new Date()
      }
      console.log("edit obj" + JSON.stringify(obj))
      this.http.post(Global.url + '/editInvSubcategories', obj)
        .subscribe(data => {
          var PartInfo = data.json();
          if (PartInfo.status == 200) {
            //console.log("Part Data" + JSON.stringify(PartInfo))
            this.toastr.success("Inventory Subcategory Updated successfully...");
            this.router.navigate(['/accountSettings/accountSettings/3/4'])
          } else {
            this.toastr.warning("Failed to Update")
          }
        }, error => {
          //console.log(error);// Error getting the data
        });
    }
    else {
      console.log("ELse block is calling")
      if (Global.documentName == '') {
        let obj = {
          "loginData": this.proData,
          "emailId": this.emailId,
          "id": this.id,
          "category": this.editInventory.value.category,
          "subCategory": this.editInventory.value.subCategory,
          "inventoryImage": '' + this.invImg,
          "createdOn": new Date()
        }
        console.log("edit obj" + JSON.stringify(obj.inventoryImage))
        this.http.post(Global.url + '/editInvSubcategories', obj)
          .subscribe(data => {
            var PartInfo = data.json();
            if (PartInfo.status == 200) {
              //console.log("Part Data" + JSON.stringify(PartInfo))
              this.toastr.success("Inventory Subcategory Updated successfully...");
              this.router.navigate(['/accountSettings/accountSettings/3/4'])
            } else {
              this.toastr.warning("Failed to Update")
            }
          }, error => {
            //console.log(error);// Error getting the data
          });

      }
    }
  }


  cancelINVItemType() {
    this.router.navigate(['/accountSettings/accountSettings/3/4'])
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list() {
    this.router.navigate(['/accountSettings/accountSettings/3/4'])
  }

  //IMAGE UPLOAD
  handleImage(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreview = reader.result;
    }
    picReader.readAsDataURL(file)
  }

  viewSubCategorie() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/viewInventorySubCategories/" + this.loginData.loginCode + '/' + this.id, options)
      .subscribe((data) => {
        var PbData = data.json();
        this.PbSubCategories = PbData.response[0];
        this.featchDetails(this.PbSubCategories)
        console.log("PbSubCategories" + JSON.stringify(this.PbSubCategories));
        if (this.PbSubCategories.inventoryImage == '') {
          this.imagePreview = "assets/img/no-image.png"
          this.invImg = '',
            console.log("Part Image" + this.imagePreview)
        } else {
          this.invImg = this.PbSubCategories.inventoryImage
          this.imagePreview = Global.url + "/" + this.PbSubCategories.inventoryImage
          console.log("invImg Image1" + this.invImg)
          Global.imagePath = this.PbSubCategories.inventoryImage
        }
      });
  }

  featchDetails(x) {
    console.log("featchDetails" + JSON.stringify(x))
    this.IdValue = x.id
    this.editInventory = this.formBuilder.group({
      category: [x.category, Validators.required],
      subCategory: [x.subCategory, Validators.required],
      picture: [x.picture, Validators.required],
    })
  }
  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.img = true;
      this.progressShow = false;
      this.progressHide = false;
      this.file2Upload = event.target.files[0];
      this.handleImage(file)
    } else {
      this.img = false;
      this.progressShow = false;
      this.progressHide = false;
      this.imagePreview = "assets/img/no-image.png"
    }
  }

  /* Image uploaded Name */
  imgfileUpload() {
    console.log("imgfileUpload calling")
    const url = Global.url + "/imageUploadData";
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append("image", this.file2Upload);
    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    xhr.open("post", url, true);
    xhr.upload.onprogress = e => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage === 100) {
          this.progressShow = false;
          this.progressHide = true;
          // this.imageUploadVariable.nativeElement.value = "";
          this.img1 = false;
        }
      }



    };
    xhr.onerror = function (e) { };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.secimagePath = a.imagePath;
      Global.documentName = a.name;

      console.log("GLObaL NAme" + Global.documentName)
    };
    xhr.send(formData);
  }
  /* Image uploaded Name */
  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  inventory() {
    this.router.navigate(['/accountSettings/accountSettings/3/4'])
  }

  subCategoryChecking() {
    console.log("This is the subCategory:" + JSON.stringify(this.editInventory.value.subCategory))
    if (this.editInventory.value.subCategory == "") {
      this.toastr.warning("subCategory is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkSubCategory/' + this.editInventory.value.subCategory + "/" + this.loginData.loginCode)
        .subscribe(data => {
          this.mailResponse = data.json();
          if (this.mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
}
