import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-equipment-status',
  templateUrl: './add-equipment-status.component.html',
  styleUrls: ['./add-equipment-status.component.scss']
})
export class AddEquipmentStatusComponent implements OnInit {
  addEquipmentStatus: FormGroup;
  loginData: any;
  submitted = false
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId
    this.addEquipmentStatus = this.formBuilder.group({
      equipmentStatus: ['', Validators.required],
      tagColor: [''],
    })
  }
  get f() { return this.addEquipmentStatus.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.addEquipmentStatus.invalid) {
      console.log("In valid")
      return;
    }
    // alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.addRole.value))
    this.saveEquipmentStatus()
  }
  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.addEquipmentStatus.value.equipmentStatus))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.addEquipmentStatus.value.equipmentStatus == "") {
      this.toastr.warning("equipmentStatus Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkEquipmentStatus/' + this.addEquipmentStatus.value.equipmentStatus + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  saveEquipmentStatus() {
    let obj = {
      "proId": this.loginData.loginCode,
      "equipmentStatus": this.addEquipmentStatus.value.equipmentStatus,
      "tagColor": this.addEquipmentStatus.value.tagColor,
      "createdBy": this.emailId,
      "status": "ACTIVE"
    }
    this.http.post(Global.url + '/addEquipmentStatus', obj)
      .subscribe(data => {
        var equipData = data.json();
        console.log("EquipmentStatus" + JSON.stringify(equipData))
        this.toastr.success('EquipmentStatus Added Successfully...');
        this.router.navigate(['/accountSettings/accountSettings/6/1'])
      }, error => {
        console.log(error);// Error getting the data
      });

    // this.router.navigate(['/proz/accountSettings'])
  }

  cancelEquipmentStatus() {
    this.router.navigate(['/accountSettings/accountSettings/6/1'])
  }

}
