import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-edit-package-name',
  templateUrl: './edit-package-name.component.html',
  styleUrls: ['./edit-package-name.component.scss']
})
export class EditPackageNameComponent implements OnInit {
  editAccountType: FormGroup
  Id: any;
  loginData: any;
  proData: any;
  submitted: boolean;
  account: any;
  mailError: boolean;
  disableSignIn: boolean;
  emailID: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public toastr: ToastrService,
    public http: Http,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.Id = this.route.snapshot.params['id']
    console.log("id" + this.Id)
    this.viewdata()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailID = this.loginData.emailId
    this.editAccountType = this.formBuilder.group({
      pbPackageName: ['', Validators.required],
      pbExpectedNetProfit: ['', Validators.required],
      colorCode: ['', Validators.required],

    })
  }
  get f() { return this.editAccountType.controls; }
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.editAccountType.invalid) {
      return;
    }
    this.updatePBPackageName()
  }
  updatePBPackageName() {
    let obj = {
      "id": this.Id,
      "proId": this.loginData.loginCode,
      "packageName": this.editAccountType.value.pbPackageName,
      "exceptedNetProfit": this.editAccountType.value.pbExpectedNetProfit,
      "colorCode": this.editAccountType.value.colorCode,
      "status": "ACTIVE",
      "modifiedBy": this.emailID
    }
    console.log("lab" + JSON.stringify(obj))
    this.http.post(Global.url + '/updatePBPackageName', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Group Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/4/3'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }

  cancelPBPackageName() {
    this.router.navigate(['/accountSettings/accountSettings/4/3'])
  }

  viewdata() {
    console.log("employee" + this.Id)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewPBPackageName/' + this.Id, options)
      .subscribe(data => {
        console.log('data' + data)
        var response = data.json();
        if (response.status == 200) {
          this.account = response.response[0];
          this.featchDetails(this.account)
          console.log("employee Data" + JSON.stringify(this.account))
        } else { }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  featchDetails(x) {
    this.editAccountType = this.formBuilder.group({
      pbPackageName: [x.packageName, Validators.required],
      pbExpectedNetProfit: [x.exceptedNetProfit, Validators.required],
      colorCode: [x.colorCode, Validators.required],
    })
  }

  accVerification() {
    console.log("teja" + JSON.stringify(this.editAccountType.value.pbPackageName))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.editAccountType.value.pbPackageName == "") {
      this.toastr.warning("markUp Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {

      this.http.get(Global.url + '/checkpackageName/' + this.editAccountType.value.pbPackageName + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
