import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';


@Component({
  providers: [Beyond],
  selector: 'app-add-source-type',
  templateUrl: './add-source-type.component.html',
  styleUrls: ['./add-source-type.component.scss']
})
export class AddSourceTypeComponent implements OnInit {
  addSourceType: FormGroup;
  loginData: any;
  submitted = false
  isSourceCode: boolean = false;
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  activeTab: Number = 0;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId
    console.log("ProId" + this.proData)
    this.addSourceType = this.formBuilder.group({
      sourceType: ['', Validators.required],
    })
  }
  // checkSourceType(){
  //   this.http.get(Global.url + '/checkSourceType/' + this.addSourceType.value.sourceType + '/' + this.proData)
  //       .subscribe(data => {
  //         var sourceResponse = data.json();
  //         if (sourceResponse.status == 'true') {
  //           console.log("Source Code alredy Existed")
  //           this.isSourceCode = true;
  //           this.disableSignIn = false;
  //         } else {
  //           console.log("Source")
  //           this.isSourceCode = false;
  //           this.disableSignIn = true;
  //         }
  //       })

  // }
  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.addSourceType.value.sourceType))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.addSourceType.value.sourceType == "") {
      this.toastr.warning("sourceType Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      var url = Global.url + '/checkSourceType/' + this.addSourceType.value.sourceType + "/" + this.loginData;
      console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkSourceType/' + this.addSourceType.value.sourceType + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  get f() { return this.addSourceType.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.addSourceType.invalid) {
      console.log("In valid")
      return;
    }
    // alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.serProzDetails.value))
    this.saveSourceType()
  }

  saveSourceType() {
    let obj = {
      "proId": this.loginData.loginCode,
      "sourceType": this.addSourceType.value.sourceType,
      "createdBy": this.emailId,
    }
    this.http.post(Global.url + '/addSourceType', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Source Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/7'])
      }, error => {
        console.log(error);// Error getting the data
      });

  }

  cancelSourceType() {
    this.router.navigate(['/accountSettings/accountSettings/7'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
