import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-expense-account-type',
  templateUrl: './add-expense-account-type.component.html',
  styleUrls: ['./add-expense-account-type.component.scss']
})
export class AddExpenseAccountTypeComponent implements OnInit {
  addExpense: FormGroup;
  loginData: any;
  submitted = false
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    public http: Http,
    public toastr: ToastrService,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId
    this.addExpense = this.formBuilder.group({
      expenseAccountType: ['', Validators.required],

    })
  }
  get f() { return this.addExpense.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.addExpense.invalid) {
      console.log("In valid")
      return;
    }
    // alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.addRole.value))
    this.saveExpenseAccountType()
  }
  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.addExpense.value.expenseAccountType))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.addExpense.value.expenseAccountType == "") {
      this.toastr.warning("expenseAccountType Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkExpenseAcc/' + this.addExpense.value.expenseAccountType + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  saveExpenseAccountType() {
    let obj = {
      "proId": this.loginData.loginCode,
      "expenseAccountType": this.addExpense.value.expenseAccountType,
      "createdBy": this.emailId,
    }
    this.http.post(Global.url + '/addExpenseAccount', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Expense Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        console.log(error);// Error getting the data
      });
    // this.router.navigate(['/proz/accountSettings'])
  }

  cancelExpenseAccountType() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
