import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';


@Component({
  providers: [Beyond],
  selector: 'app-edit-source-type',
  templateUrl: './edit-source-type.component.html',
  styleUrls: ['./edit-source-type.component.scss']
})
export class EditSourceTypeComponent implements OnInit {
  editSourceType: FormGroup;
  loginData: any;
  submitted = false
  isSourceCode: boolean = false;
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  emailId: any;
  viewsource: any;
  sourceType: string;
  id: string;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.id = this.route.snapshot.params['id']
    console.log("This is  id" + this.id)
    this.viewSourceValues()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId
    console.log("ProId" + this.proData)
    this.editSourceType = this.formBuilder.group({
      sourceType: ['', Validators.required],
    })
  }
  // checkSourceType(){
  //   this.http.get(Global.url + '/checkSourceType/' + this.editSourceType.value.sourceType + '/' + this.proData)
  //       .subscribe(data => {
  //         var sourceResponse = data.json();
  //         if (sourceResponse.status == 'true') {
  //           console.log("Source Code alredy Existed")
  //           this.isSourceCode = true;
  //           this.disableSignIn = false;
  //         } else {
  //           console.log("Source")
  //           this.isSourceCode = false;
  //           this.disableSignIn = true;
  //         }
  //       })

  // }
  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.editSourceType.value.sourceType))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.editSourceType.value.sourceType == "") {
      this.toastr.warning("sourceType Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      var url = Global.url + '/checkSourceType/' + this.editSourceType.value.sourceType + "/" + this.loginData;
      console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkSourceType/' + this.editSourceType.value.sourceType + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          console.log("MAILRESPONSE" + JSON.stringify(mailResponse.response))
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  get f() { return this.editSourceType.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.editSourceType.invalid) {
      console.log("In valid")
      return;
    }
    // alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.serProzDetails.value))
    this.saveSourceType()
  }

  saveSourceType() {
    // if (this.editSourceType.value.sourceType ==) { }
    // else {
    let obj = {
      "proId": this.loginData.loginCode,
      "sourceType": this.editSourceType.value.sourceType,
      "id": this.id,
      "createdBy": this.emailId,
    }
    this.http.post(Global.url + '/editSourceTypeData', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Source Tupe Updated Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/7'])
      }, error => {
        console.log(error);// Error getting the data
      });
    // }


  }
  viewSourceValues() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewSource/' + this.id, options)
      .subscribe(data => {
        var viewRoleData = data.json();
        console.log("THIS is view" + JSON.stringify(viewRoleData))
        this.viewsource = viewRoleData.response[0];
        this.editdata(this.viewsource)
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  editdata(data) {
    console.log("view data variable" + JSON.stringify(data))
    this.editSourceType = this.formBuilder.group({
      sourceType: [data.sourceType, Validators.required],

    })
  }
  cancelSourceType() {
    this.router.navigate(['/accountSettings/accountSettings/7'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
