import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Beyond } from '../../../../../utilities/beyond';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-edit-waive-of-right',
  templateUrl: './edit-waive-of-right.component.html',
  styleUrls: ['./edit-waive-of-right.component.scss']
})
export class EditWaiveOfRightComponent implements OnInit {


  editwaiveOfRight: FormGroup
  Id: any;
  loginData: any;
  proData: any;
  submitted: boolean;
  completionId: boolean = false;
  RequiredInvoceCompletion: boolean = true;
  account: any;
  checkedValues: any;
  emailId: any;

  constructor(
    public beyond: Beyond,
    public router: Router,
    public toastr: ToastrService,
    public http: Http,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute, ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.Id = this.route.snapshot.params['id']
    console.log("id" + this.Id)
    this.viewdata()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId
    this.editwaiveOfRight = this.formBuilder.group({
      waiveOfRight: ['', Validators.required],
      RequiredInvoceCompletion: ['', Validators.required],

    })
  }
  get f() { return this.editwaiveOfRight.controls; }
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.editwaiveOfRight.invalid) {
      return;
    }

  }
  EditwaiveOfRight() {
    let obj = {
      "id": this.Id,
      "waiveOfRight": this.editwaiveOfRight.value.waiveOfRight,
      "RequiredInvoceCompletion": this.editwaiveOfRight.value.RequiredInvoceCompletion,
      "modifiedBy": this.emailId,
      "status": "ACTIVE"
    }
    console.log("waiveOfRight" + JSON.stringify(obj))
    this.http.post(Global.url + '/editWaiveofright', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Group Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }

  cancelwaiveOfRight() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }

  viewdata() {
    console.log("employee" + this.Id)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewWaiveofRight/' + this.Id, options)
      .subscribe(data => {
        console.log('data' + data)
        var response = data.json();
        if (response.status == 200) {
          this.account = response.response[0];
          this.checkedValues = this.account.RequiredInvoceCompletion
          this.featchDetails(this.account)
          console.log("employee Data" + JSON.stringify(this.account))
        } else { }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  featchDetails(x) {
    this.editwaiveOfRight = this.formBuilder.group({
      waiveOfRight: [x.waiveOfRight, Validators.required],
      RequiredInvoceCompletion: [x.RequiredInvoceCompletion, Validators.required],
    })
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}