import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProzAccountSettingsLayoutComponent } from './proz-account-settings-layout/proz-account-settings-layout.component';
import { AccountSettingsComponent } from './proz-account-settings/account-settings/account-settings.component';
import { AddEquipmentItemTypeComponent } from './proz-account-settings/equipment/add-equipment-item-type/add-equipment-item-type.component';
