import { AddGroupComponent } from './proz-account-settings/general/add-group/add-group.component';
import { EditGroupComponent } from './proz-account-settings/general/edit-group/edit-group.component';
import { AddExpenseAccountTypeComponent } from './proz-account-settings/accounting/add-expense-account-type/add-expense-account-type.component';
import { EditChangeOrdersComponent } from './proz-account-settings/invoice/edit-change-orders/edit-change-orders.component';
import { EditWaiveOfRightComponent } from './proz-account-settings/invoice/edit-waive-of-right/edit-waive-of-right.component';
import { EditCompletionComponent } from './proz-account-settings/invoice/edit-completion/edit-completion.component';
import { EditAuthorizationTncComponent } from './proz-account-settings/invoice/edit-authorization-tnc/edit-authorization-tnc.component';
