import { AddMessagesAlertsComponent } from './proz-account-settings/messages-alerts/add-messages-alerts/add-messages-alerts.component';
import { EditProLoginAgreementComponent } from './proz-account-settings/staff-roles-permissions/edit-pro-login-agreement/edit-pro-login-agreement.component';
import { AddProLoginAgreementComponent } from './proz-account-settings/staff-roles-permissions/add-pro-login-agreement/add-pro-login-agreement.component';
import { EditProEquipmentUseAgreementComponent } from './proz-account-settings/staff-roles-permissions/edit-pro-equipment-use-agreement/edit-pro-equipment-use-agreement.component';
import { AddPackageNameComponent } from './proz-account-settings/price-book/package-names/add-package-name/add-package-name.component';
import { EditPackageNameComponent } from './proz-account-settings/price-book/package-names/edit-package-name/edit-package-name.component';
import { AddPurchaseOrderComponent } from './proz-account-settings/purchase-order/add-purchase-order/add-purchase-order.component';
import { AddPromoCodeComponent } from './proz-account-settings/add-promo-code/add-promo-code.component';
import { EditSourceTypeComponent } from './proz-account-settings/sales-and-marketing/add-source-type/edit-source-type/edit-source-type.component';
import { VendorCategoryComponent } from './proz-account-settings/vendor-category/vendor-category/vendor-category.component';
import { PreworkInspectionComponent } from './proz-account-settings/prework-inspection/prework-inspection.component';
import { PostworkInspectionComponent } from './proz-account-settings/postwork-inspection/postwork-inspection.component';
import { AddPBSubCategoriesComponent } from './proz-account-settings/PBSubCategories/add-pbsub-categories/add-pbsub-categories.component';
import { EditPBSubCategoriesComponent } from './proz-account-settings/PBSubCategories/edit-pbsub-categories/edit-pbsub-categories.component';
import { EditInventorySubcategoryComponent } from './proz-account-settings/account-settings/edit-inventory-subcategory/edit-inventory-subcategory.component';
import { EditEquipmentSubcategoryComponent } from './proz-account-settings/account-settings/edit-equipment-subcategory/edit-equipment-subcategory.component';

const routes: Routes = [{


  path: '', component: ProzAccountSettingsLayoutComponent,
  children: [

    /* -- Account Settings -- */
    {
      path: 'accountSettings/:tab',
      component: AccountSettingsComponent
    },
    {
      path: 'accountSettings/:tab/:ctab',
      component: AccountSettingsComponent
    },
    {
      path: 'accountSettings',
      component: AccountSettingsComponent
    },
    // {
    //   path: 'equipmentSettings',
    //   component: EquipmentComponent
    // },
    // {
    //   path: 'servicePackagesSettings',
    //   component: ServicePackagesComponent
    // },
    // {
    //   path: 'accountingSettings',
    //   component: AccountingComponent
    // },


    {
      path: 'addEquipItemType',
      component: AddEquipmentItemTypeComponent
    },
    {
      path: 'addEquipmentStatus',
      component: AddEquipmentStatusComponent
    },
    {
      path: 'addSourceType',
      component: AddSourceTypeComponent
    },
    {
      path: 'editSourceType/:id',
      component: EditSourceTypeComponent
    },


    {
      path: 'addGeneralGroup',
      component: AddGroupComponent
    },
    {
      path: 'addAlertsMessages',
      component: AddMessagesAlertsComponent
    },
    {
      path: 'editGeneralGroup/:id',
      component: EditGroupComponent
    },
    {
      path: 'addExpenseAccountType',
      component: AddExpenseAccountTypeComponent
    },
    {
      path: 'roleMatrix/:string/:anyCode',
      component: RoleMatrixComponent
    },
    {
      path: 'addProStaffRole',
      component: AddProStaffRoleComponent
    },
    {
      path: 'editProStaffRole/:id',
      component: EditProStaffRoleComponent
    },
    // {
    //   path: 'addEquipmentUseAgreement',
    //   component: AddProEquipmentUseAgreementComponent
    // },
    {
      path: 'editEquipmentUseAgreement/:id',
      component: EditProEquipmentUseAgreementComponent
    },
    {
      path: 'addLoginAgreement',
      component: AddProLoginAgreementComponent
    },
    {
      path: 'editLoginAgreement/:id',
      component: EditProLoginAgreementComponent
    },
    {
      path: 'addPBPackageName',
      component: AddPackageNameComponent,
    },
    {
      path: 'editPBPackageName/:id',
      component: EditPackageNameComponent,
    },
    // },
    // {
    //   path: 'addPBMarkUp',
    //   component: AddPbMarkupsComponent,
    // },
    // {
    //   path: 'editPBMarkUp/:id',
    //   component: EditPbMarkupsComponent,
    // },

    {
      path: 'addINVItemType',
      component: AddInvItemTypeComponent,
    },

    {
      path: 'customerPreworkInspection',
      component: PreworkInspectionComponent,
    },
    {
      path: 'customerPostworkInspection',
      component: PostworkInspectionComponent,
    },

    {
      path: 'editAuthorizationTNC/:id',
      component: EditAuthorizationTncComponent
    },
    {
      path: 'editCompletion/:id',
      component: EditCompletionComponent
    },
    {
      path: 'editWaiveOfRight/:id',
      component: EditWaiveOfRightComponent
    },
    {
      path: 'editChangeOrders/:id',
      component: EditChangeOrdersComponent
    },
    {
      path: 'purchaseOrderSettings',
      component: AddPurchaseOrderComponent
    },
    {
      path: 'addPromoCode',
      component: AddPromoCodeComponent
    },
    {
      path: 'addVendorCategory',
      component: VendorCategoryComponent
    },
    {
      path: 'addPBSubCategories',
      component: AddPBSubCategoriesComponent
    },
    {
      path: 'editPBSubCategories/:id',
      component: EditPBSubCategoriesComponent
    },
    {
      path: 'editInventorySubCategorie/:id',
      component: EditInventorySubcategoryComponent
    },
    {
      path: 'editEquipmentSubCategorie/:id',
      component: EditEquipmentSubcategoryComponent
    },
    /* -- /Account Settings -- */


  ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProzAccountSettingsRoutingModule { }
