import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-promo-code',
  templateUrl: './add-promo-code.component.html',
  styleUrls: ['./add-promo-code.component.scss']
})
export class AddPromoCodeComponent implements OnInit {
  addPromoCode: FormGroup
  loginData: any;
  proData: any;
  mailError: boolean;
  disableSignIn: boolean = false;
  emailId: any;
  activeTab: Number = 0;
  submitted: boolean = false;


  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.addPromoCode = this.formBuilder.group({
        promoCodeDiscount: ['', Validators.required],
        discountAmount: ['', Validators.required],
      })
  }





  get f() { return this.addPromoCode.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid

    if (this.addPromoCode.value.promoCodeDiscount == "" || this.mailError == true) {
      return
    } else if (this.addPromoCode.value.discountAmount == "") {
      return
    }
    else {

      this.savePromoCode()
    }
  }

  promoCodeVerification() {
    console.log("This is the Promo Code Name:" + JSON.stringify(this.addPromoCode.value.promoCodeDiscount))
    console.log("This is the ProID:" + JSON.stringify(this.proData))
    if (this.addPromoCode.value.promoCodeDiscount == "") {
      this.toastr.warning("Promo Code/ Discount is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkPromoCode/' + this.addPromoCode.value.promoCodeDiscount + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

  savePromoCode() {
    let obj = {
      "proId": this.proData,
      "promoCodeDiscount": this.addPromoCode.value.promoCodeDiscount,
      "discountAmount": this.addPromoCode.value.discountAmount,
      //"status": "ACTIVE",
      "createdBy": this.proData,
      // "createdOn": new Date(),
    }
    // console.log("this is obj" + JSON.stringify(obj))
    this.http.post(Global.url + '/addPromoCodes', obj)
      .subscribe(data => {
        var promoCodeData = data.json();
        if (promoCodeData.status == 200) {
          console.log("rolesData" + JSON.stringify(promoCodeData))
          this.toastr.success('Promo Code Added Successfuly...');
          this.router.navigate(['/accountSettings/accountSettings/10'])
        } else {
          this.toastr.warning('Failed to Add Promo Code...');
        }
      }, error => {
        console.log(error);// Error getting the data
      });
  }

  cancelPromoCode() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}