  // ROLES
  addRole() {
    this.router.navigate(["/accountSettings/addProStaffRole"]);
  }
  editStaffRole(data) {
    this.router.navigate(["/accountSettings/editProStaffRole", data]);
  }

  getRoles() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(
        Global.url + "/getStaffRoleProSpe/" + this.loginData.loginCode,
        options
      )
      .subscribe((data) => {
        var roles = data.json();
        if (roles.status == 200) {
          this.roleData = roles.response;
          console.log("role data" + JSON.stringify(this.roleData));
          this.roleData.forEach((e) => {
            if (
              e.permissions === "" ||
              e.permissions === null ||
              e.permissions === undefined
            ) {
              e.roleStatus = "NO PERMISSION";
            } else {
              e.roleStatus = "PERMISSIONS";
            }
          });
          this.dataRoles = true;
          console.log("service category Data" + JSON.stringify(this.roleData));
        } else {
          this.dataRoles = false;
        }
      });
  }
