  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          this.categoryCode = this.categoryDet[0].categoryCode
          this.categoryName = this.categoryDet[0].categoryName
          console.log("default category" + JSON.stringify(this.categoryDet[0].categoryCode))
          this.defaultSubCategories(this.categoryCode)
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  defaultSubCategories(categoryCode) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/defaulProSubCategory/' + this.categoryCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.defaultSubcategoryData = response.response;
          console.log("defaultSubCategories" + JSON.stringify(this.defaultSubcategoryData))
          this.addlistProServices()
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }


  addlistProServices() {
    console.log("matchingProData" + JSON.stringify(this.defaultSubcategoryData))
    this.http.post(Global.url + '/addInvSubcategories' + '/' + this.loginData.loginCode, this.defaultSubcategoryData)
      .subscribe(data => {
        var matchingData = data.json();
        this.toastr.success("Default Inventory SubCategories Added Successfully");
        this.matchingResponse = matchingData.response;
        if (matchingData.status == 200) {
          this.listInventory()
          this.matchingResponseStatus = false;
          // this.toastr.success("Matching Data Added Successfully");
        }
        else {
          this.toastr.warning("Record adding Failed")
          this.matchingResponseStatus = true;
        }
      },
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        })
  }
