  viewPostWorkQuestion(question) {
    if (question) {
      // if fileds are below 4 then adding the remaing fileds.
      let options = (question.options || "").split(";");
      if (options.length < 4) {
        options = [].concat(options);
        options = options.concat(new Array(4 - options.length).fill(0).map((o) => ""));
      }
      // console.log("AccountSettingsComponent -> viewPreWorkQuestion -> options", options)
      this.addPostQuestion = this.formBuilder.group({
        id: question.id,
        question: [question.question, Validators.required],
        optionType: [question.optionType, Validators.required],
        options: this.formBuilder.array(
          options.map((value) => this.formBuilder.group({ value }))
        ),
      });
    } else {
      this.postpareQuestionForm();

    }

  }


