  onChangeNotification(notification, prop, event) {
    this.notifications = this.notifications.map((o) => {
      if (notification.id !== o.id) return o;
      return { ...o, [prop]: event.target.value };
    });
  }

  onEstimateValueChange(estimate, msgValue) {
    this.estimate = this.estimate.map((e) => {
      if (estimate.id !== e.id) return e;
      return { ...e, msgValue };
    });
  }

  onInvoiceChange(prop, value) {
    this.invoiceForm[prop] = value;
  }

