  addSettings() {
    const proId = this.loginData.loginCode;
    const createdBy = this.emailId;
    const { msgValue, msgId } = this.invoiceForm;
    const request = {
      proId,
      createdBy,
      estimate: this.estimate,
      messages: this.notifications,
      invoice: { msgValue, msgId },
    };
    console.log("AccountSettingsComponent -> addSettings -> request", request);

    this.http.post(`${Global.url}/account-settings`, request).subscribe(
      (data) => {
        const msgData = data.json();
        if (msgData.status === 200) {
          this.toastr.success("Settings saved Successfully.");
        } else {
          this.toastr.warning("Saving settings failed.");
        }
      },
      (error) => this.toastr.error(error.message)
    );
  }

  resetSettings() {
    this.invoiceForm = { ...invoiceObj };
    this.notifications = notificationsFields.slice();
    this.estimate = estimateFields.slice();
  }

  getSettings() {
    const proId = this.loginData.loginCode;
    this.http.get(`${Global.url}/account-settings?proId=${proId}`).subscribe(
      (data) => {
        this.invoiceForm = { ...invoiceObj };
        const json = data.json();
        console.log("AccountSettingsComponent -> getSettings -> json", json);
        if (json.status === 200) {
          const { messages, invoice, estimate } = json.response;
          const [_invoice = invoiceObj] = invoice || [invoiceObj];
          this.invoiceForm = _invoice;
          if (Array.isArray(messages) && messages.length) {
            this.notifications = messages.map((o) => ({
              ...o,
              typeDesc: "Send Notification To Staff",
            }));
          }
          if (Array.isArray(estimate) && estimate.length) {
            this.estimate = estimate.map((o, id) => {
              if (id) return o;
              return { ...o, type: "textarea" };
            });
          }
        }
      },
      (error) => this.toastr.error(error.message)
    );
  }
  checkChange() {
    if (
      this.addPreQuestion.value.optionType == "CHECK" ||
      this.addPreQuestion.value.optionType == "RADIO"
    ) {
      // this.questionList.get("options").enable()
      this.addPreQuestion.controls.options.enable();
    } else {
      if (
        this.addPreQuestion.value.optionType == "TEXT" ||
        this.addPreQuestion.value.optionType == "YESNO"
      ) {
        this.addPreQuestion.controls.options.disable();
      }
    }
  }
  checkChangepost() {
    if (
      this.addPostQuestion.value.optionType == "CHECK" ||
      this.addPostQuestion.value.optionType == "RADIO"
    ) {
      // this.questionList.get("options").enable()
      this.addPostQuestion.controls.options.enable();
    } else {
      if (
        this.addPostQuestion.value.optionType == "TEXT" ||
        this.addPostQuestion.value.optionType == "YESNO"
      ) {
        this.addPostQuestion.controls.options.disable();
      }
    }
  }
