  saveQuestion(request) {
    this.http.post(Global.url + "/addNewpreworkquestions", request).subscribe(
      (data) => {
        const questionsData = data.json();
        if (questionsData.status == 200) {
          $("#addNewquestion").modal("hide");
          this.listQuestions();
          this.addPreQuestion.reset();
        }
      },
      (error) => console.log(error)
    );
  }

  updateQuestion(request) {
    this.http.post(Global.url + "/editNewpreworkquestions", request).subscribe(
      (data) => {
        const questionsData = data.json();
        if (questionsData.status == 200) {
          $("#addNewquestion").modal("hide");
          this.listQuestions();
          this.addPreQuestion.reset();
        }
      },
      (error) => console.log(error)
    );
  }

  addOrUpdateQuestion() {
    const { question, optionType, options, id } = this.addPreQuestion.value;
    // console.log("AccountSettingsComponent -> addOrUpdateQuestion -> options", options)
    const request = {
      id,
      question,
      optionType,
      proId: this.loginData.loginCode,
      options: (options || []).map((o) => o.value).filter(o => o).join(";"),
    };
    if (id) {
      this.updateQuestion(request);
    } else {
      this.saveQuestion(request);
    }
  }

