import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-pro-login-agreement',
  templateUrl: './edit-pro-login-agreement.component.html',
  styleUrls: ['./edit-pro-login-agreement.component.scss']
})
export class EditProLoginAgreementComponent implements OnInit {
  editLoginAgreement: FormGroup;
  agreementList: any;
  private formSumitAttempt: boolean;
  id: any;
  emailId: any;
  loginData: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.id = this.route.snapshot.params['id'];
    console.log("id=" + this.id)
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.emailId = this.loginData.emailId
    this.listAgreement()
    this.editLoginAgreement = this.formBuilder.group({
      logInAgreement: ['', Validators.required],
      requiredToLogin: ['', Validators.required],
    })
  }

  update() {
    this.formSumitAttempt = true;
    if (this.editLoginAgreement.valid) {
      console.log('form submitted');
    }
    if (this.editLoginAgreement.value.requiredToLogin == false) {
      this.editLoginAgreement.value.requiredToLogin = ""
    }
    let obj = {
      "id": this.id,
      "logInAgreement": this.editLoginAgreement.value.logInAgreement,
      "requiredToLogin": this.editLoginAgreement.value.requiredToLogin,
      "modifiedBy": this.emailId,
      "status": "ACTIVE"

    }
    console.log("editAgreement" + JSON.stringify(obj))
    this.http.post(Global.url + '/editAgreement', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("addAgreement" + JSON.stringify(groupData))
        this.toastr.success('Agreement edited Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        console.log(error);// Error getting the data
      });

  }


  cancel() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }

  editdata(data) {
    console.log("view data variable" + JSON.stringify(data))
    this.editLoginAgreement = this.formBuilder.group({
      logInAgreement: [data.logInAgreement, Validators.required],
      requiredToLogin: [data.requiredToLogin, Validators.required],

    })
  }
  // LIST Agreement STARTS
  listAgreement() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewAgreement/' + this.id, options)
      .subscribe(data => {
        var agreement = data.json();
        if (agreement.status == 200) {

          console.log("agreementStatus Details" + JSON.stringify(agreement))
          this.agreementList = agreement.response[0];
          this.editdata(this.agreementList)
          console.log("list" + JSON.stringify(this.agreementList))
          return false;
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
  // LIST Agreement ENDS
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
