import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-pbsub-categories',
  templateUrl: './edit-pbsub-categories.component.html',
  styleUrls: ['./edit-pbsub-categories.component.scss']
})
export class EditPBSubCategoriesComponent implements OnInit {
  loginData: any;
  emailId: any;
  proData: any;
  submitted: boolean;
  editPBSubCategories: FormGroup;
  PbSubCategories: any;
  id: void;
  IdValue: any;
  categoryDet: any;
  mailError: boolean;
  disableSignIn: boolean;
  mailResponse: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
  ) { }
  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId,
      this.id = this.route.snapshot.params['id']
    console.log("id" + this.id)
    this.viewPBSubCategorie()
    this.editPBSubCategories = this.formBuilder.group({
      category: ['', Validators.required],
      subCategory: ['', Validators.required],
    })
    this.listCategories()
  }
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.editPBSubCategories.invalid) {
      console.log("!!")
      this.savePBSubCategories()
      return;
    }

  }

  get f() { return this.editPBSubCategories.controls; }
  savePBSubCategories() {
    console.log(this.editPBSubCategories.value.category)
    console.log(this.editPBSubCategories.value.subCategory)
    if (this.editPBSubCategories.value.category != '' && this.editPBSubCategories.value.subCategory != '' && this.mailResponse.status == "FALSE") {
      let obj = {
        "proId": this.proData,
        'id': this.IdValue,
        "emailId": this.emailId,
        "category": this.editPBSubCategories.value.category,
        "subCategory": this.editPBSubCategories.value.subCategory,
        "createdOn": new Date(),
        "createdBy": this.emailId
      }
      console.log("this is obj" + JSON.stringify(obj))
      this.http.post(Global.url + '/updatePBSubCategory', obj)
        .subscribe(data => {
          var rolesData = data.json();
          console.log("rolesData" + JSON.stringify(rolesData))
          this.toastr.success('PriceBook SubCategory updated Successfuly...');
          this.router.navigate(['/accountSettings/accountSettings/4/6'])
        }, error => {
          console.log(error);// Error getting the data
        });
    }
  }

  viewPBSubCategorie() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/viewPBSubCategories/" + this.loginData.loginCode + '/' + this.id, options)
      .subscribe((data) => {
        var PbData = data.json();
        if (PbData.status == 200) {
          this.PbSubCategories = PbData.response[0];
          this.featchDetails(this.PbSubCategories)
          console.log("PbSubCategories" + JSON.stringify(this.PbSubCategories));

        } else {

        }
      });
  }

  featchDetails(x) {
    console.log("featchDetails" + JSON.stringify(x))
    this.IdValue = x.id
    this.editPBSubCategories = this.formBuilder.group({
      category: [x.category, Validators.required],
      subCategory: [x.subCategory, Validators.required],
    })
  }
  listaccount() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }
  cancelPBSubCategories() {
    this.router.navigate(['/accountSettings/accountSettings/4/6'])
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list() {
    this.router.navigate(['/accountSettings/accountSettings/4/6'])
  }
  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  subCategoryChecking() {
    console.log("This is the subCategory:" + JSON.stringify(this.editPBSubCategories.value.subCategory))
    if (this.editPBSubCategories.value.subCategory == "") {
      this.toastr.warning("subCategory is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkPBSubCategory/' + this.editPBSubCategories.value.subCategory + "/" + this.loginData.loginCode)
        .subscribe(data => {
          this.mailResponse = data.json();
          if (this.mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

}
