import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-group',
  templateUrl: './edit-group.component.html',
  styleUrls: ['./edit-group.component.scss']
})
export class EditGroupComponent implements OnInit {
  id: any;
  editGroup: FormGroup;
  groupData: any;
  loginData: any;
  categoryDet: any;
  statesData: any;
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.id = this.route.snapshot.params['id'];
    console.log("account setting id" + this.id)
    this.getListOfStates();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    console.log("this is login Data" + JSON.stringify(this.loginData))
    this.proData = this.loginData.loginCode
    this.emailId = this.loginData.emailId
    this.listCategories();
    this.viewGroup();
    this.editGroup = this.formBuilder.group({
      groupName: ['', Validators.required],
      phoneNumber: ['', Validators.required],
      address: ['', Validators.required],
      serviceCategory: ['', Validators.required],
      salesTax: ['', Validators.required],
      timeZone: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      zipCode: ['', Validators.required],
    })

  }
  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.editGroup.value.groupName))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.editGroup.value.groupName == "") {
      this.toastr.warning("groupName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkGroupName/' + this.editGroup.value.groupName + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  updateGroup() {
    let obj = {
      "id": this.id,
      "proId": this.loginData.loginCode,
      "groupName": this.editGroup.value.groupName,
      "phoneNumber": this.editGroup.value.phoneNumber,
      "groupAddress": this.editGroup.value.address,
      "serviceCategory": this.editGroup.value.serviceCategory,
      "salesTax": this.editGroup.value.salesTax,
      "city": this.editGroup.value.city,
      "state": this.editGroup.value.state,
      "zipCode": this.editGroup.value.zipCode,
      "timeZone": this.editGroup.value.timeZone,
      "status": "ACTIVE",
      "modifiedBy": this.emailId,


    }
    console.log("this Updated object " + JSON.stringify(obj))
    this.http.post(Global.url + '/editProGroup', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Group edited Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        console.log(error);// Error getting the data
      });

    // this.router.navigate(['/proz/accountSettings'])
  }

  cancelGroup() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }

  // View Group starts
  viewGroup() {
    console.log("view group id" + this.id)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewStaffGroups/' + this.id, options)
      .subscribe(data => {
        var group = data.json();
        this.groupData = group.response[0];
        this.editdata(this.groupData)
        console.log("View customer Data" + JSON.stringify(this.groupData))

        //   }, error => {
        //     this.toastr.warning('Unable to Connect to Server')
      })
  }

  editdata(data) {
    console.log("view data variable" + JSON.stringify(data))
    this.editGroup = this.formBuilder.group({
      groupName: [data.groupName, Validators.required],
      phoneNumber: [data.phone, Validators.required],
      address: [data.groupAddress, Validators.required],
      serviceCategory: [data.serviceCategory, Validators.required],
      salesTax: [data.salesTax, Validators.required],
      timeZone: [data.timeZone, Validators.required],
      city: [data.city, Validators.required],
      zipCode: [data.zipCode, Validators.required],
      state: [data.state, Validators.required],
    })
  }
  //   // View Group Ends

  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.proData, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/accountSettings/accountSettings'])
  }
}
