import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-purchase-order',
  templateUrl: './add-purchase-order.component.html',
  styleUrls: ['./add-purchase-order.component.scss']
})
export class AddPurchaseOrderComponent implements OnInit {
  purchaseOrdersForm: FormGroup;
  loginData: any;
  submitted = false
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.purchaseOrdersForm = this.formBuilder.group({
      purchaseOrders: ['', Validators.required],
    })
  }
  get f() { return this.purchaseOrdersForm.controls; }

  onSubmit() {
    this.submitted = true;
    if (this.purchaseOrdersForm.invalid) {
      console.log("In valid")
      return;
    }
    this.AddPurchaseOrders()
  }

  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.purchaseOrdersForm.value.equipmentItemType))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.purchaseOrdersForm.value.equipmentItemType == "") {
      this.toastr.warning("equipmentItemType Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.purchaseOrdersForm.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkEquipmentItemType/' + this.purchaseOrdersForm.value.equipmentItemType + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  AddPurchaseOrders() {
    let obj = {
      "proId": this.loginData.loginCode,
      "purchaseOrders": this.purchaseOrdersForm.value.purchaseOrders,
    }
    this.http.post(Global.url + '/addPurchaseOrders', obj)
      .subscribe(data => {
        var equipmentData = data.json();
        console.log("addPurchaseOrders" + JSON.stringify(equipmentData))
        this.toastr.success('Equipment Data Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }

  cancelItemType() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
