  //     this.disableSignIn = true;
  //     return false;
  //   } else {
  //     // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
  //     // console.log("url" + JSON.stringify(url))
  //     this.http.get(Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.proData)
  //       .subscribe(data => {
  //         var mailResponse = data.json();
  //         if (mailResponse.status == "TRUE") {
  //           this.mailError = true;
  //           this.disableSignIn = false;
  //           return false
  //           // console.log("hao")
  //         } else {
  //           this.mailError = false;
  //           this.disableSignIn = true;
  //         }
  //       })
  //   }
  // }
  
  saveItemType() 
  {
    if (this.addEquipmentItem.value.category != undefined && this.addEquipmentItem.value.subCategory != '')
    {
    let obj = {
      "proId": this.loginData.loginCode,
      //"emailId": this.emailId,
      "category": this.addEquipmentItem.value.category,
      "subCategory": this.addEquipmentItem.value.subCategory,
      //"picture": '' + Global.documentName,
      //"createdOn": new Date()
    }
    this.http.post(Global.url + '/addEquipmentSubCategory', obj)
      .subscribe(data => {
        var equipmentData = data.json();
        console.log("groupData" + JSON.stringify(equipmentData))
        this.toastr.success('Equipment Data Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/6/0'])
      }, error => {
        console.log(error);// Error getting the data
      });
    // this.router.navigate(['/proz/accountSettings'])
  }
}

cancelINVItemType() {
    this.router.navigate(['/accountSettings/accountSettings/6/0'])
  }

  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }

  subCategoryChecking() {
    console.log("This is the subCategory:" + JSON.stringify(this.addEquipmentItem.value.subCategory))
    if (this.addEquipmentItem.value.subCategory == "") {
      this.toastr.warning("subCategory is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkEquipmentSubCategory/' + this.addEquipmentItem.value.subCategory + "/" + this.loginData.loginCode)
        .subscribe(data => {
          this.mailResponse = data.json();
          console.log("MAIRESPONCE#" + JSON.stringify(this.mailResponse))
          if (this.mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
