import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { CustomValidators } from '../../../../../utilities/customValidators';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond, CustomValidators],
  selector: 'app-add-group',
  templateUrl: './add-group.component.html',
  styleUrls: ['./add-group.component.scss']
})
export class AddGroupComponent implements OnInit {
  addGroup: FormGroup;
  loginData: any;
  categoryDet: any = [];
  statesData: any;
  submitted = false
  disableSignIn: boolean = false;
  groupSave :boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.getListOfStates();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.emailId = this.loginData.emailId
    this.listCategories();
    this.addGroup = this.formBuilder.group({
      groupName: ['', Validators.required],
      phoneNumber: new FormControl("", Validators.compose([CustomValidators.mobileNumValidator, Validators.required])),
      address: ['', Validators.required],
      serviceCategory: ['', Validators.required],
      salesTax: ['', Validators.required],
      timeZone: ['', Validators.required],
      city: ['', Validators.required],
      // state: ['', Validators.required],
      zipCode: ['', Validators.required],
      state: ['', Validators.required],
    })

  }
  get f() { return this.addGroup.controls; }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.addGroup.invalid) {
      return;
    }
    this.saveGroup()
  }
  mailIdVerification() {
    console.log("This is the Group Name:" + JSON.stringify(this.addGroup.value.groupName))
    console.log("This is the ProID:" + JSON.stringify(this.proData))
    if (this.addGroup.value.groupName == "") {
      this.toastr.warning("groupName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkGroupName/' + this.addGroup.value.groupName + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  saveGroup() {
    this.groupSave = true
    let obj = {
      "proId": this.loginData.loginCode,
      "groupName": this.addGroup.value.groupName,
      "phoneNumber": this.addGroup.value.phoneNumber,
      "groupAddress": this.addGroup.value.address,
      "serviceCategory": this.addGroup.value.serviceCategory,
      "salesTax": this.addGroup.value.salesTax,
      "timeZone": this.addGroup.value.timeZone,
      "city": this.addGroup.value.city,
      // "state": this.addGroup.value.state,
      "zipCode": this.addGroup.value.zipCode,
      "state": this.addGroup.value.state,
      "createdBy": this.emailId,
      "status": "ACTIVE"
    }
    this.http.post(Global.url + '/addProGroup', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Group Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        this.groupSave = false
        console.log(error);// Error getting the data
      });

  }

  cancelGroup() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }

  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.proData, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found for Categories")

        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  getListOfStates() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOfStates', options)
      .subscribe(data => {
        var response = data.json();
        this.statesData = response.response;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/accountSettings/accountSettings'])
  }
}
