import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-edit-equipment-subcategory',
  templateUrl: './edit-equipment-subcategory.component.html',
  styleUrls: ['./edit-equipment-subcategory.component.scss']
})
export class EditEquipmentSubcategoryComponent implements OnInit {
  editEquipment: FormGroup
  loginData: any;
  Id: any;
  proData: any;
  categoryDet: any;
  EqSubCategories: any;
  IdValue: any;
  disableSignIn: boolean;
  mailResponse: any;
  mailError: boolean;
  submitted: boolean;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.Id = this.route.snapshot.params['id']   
    console.log("id" + this.Id)
    this.proData = this.loginData.loginCode,
      this.listCategories()
    this.viewSubCategorie()
    this.editEquipment = this.formBuilder.group({
      category: ['', Validators.required],
      subCategory: ['', Validators.required],
      //picture: ['', Validators.required],
    })
  }
  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }
  viewSubCategorie() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/viewEquipmentSubCategories/" + this.loginData.loginCode + '/' + this.Id, options)
      .subscribe((data) => {
        var PbData = data.json();
        this.EqSubCategories = PbData.response[0];
        this.featchDetails(this.EqSubCategories)
        console.log("EqSubCategories" + JSON.stringify(this.EqSubCategories));       
      });
  }
  featchDetails(x) {
    console.log("featchDetails" + JSON.stringify(x))
    this.IdValue = x.id
    this.editEquipment = this.formBuilder.group({
      category: [x.category, Validators.required],
      subCategory: [x.subCategory, Validators.required],
      //picture: [x.picture, Validators.required],
    })
  }

  subCategoryChecking() {
    console.log("This is the subCategory:" + JSON.stringify(this.editEquipment.value.subCategory))
    if (this.editEquipment.value.subCategory == "") {
      this.toastr.warning("subCategory is Empty")
      //this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkEqSubCategory/' + this.editEquipment.value.subCategory + "/" + this.loginData.loginCode)
        .subscribe(data => {
          this.mailResponse = data.json();
          if (this.mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            //this.disableSignIn = true;
          }
        })
    }
  }
  onSubmit() {
    this.submitted = true;
    console.log("this.editEquipment.valid" + this.editEquipment.valid)
    console.log("this.editEquipment.invalid" + this.editEquipment.invalid)
    if (this.editEquipment.value.category != undefined && this.editEquipment.value.subCategory != '') {
      this.updateInvSubCategories()
      return;
    }
    else {

    }

  }
  get f() { return this.editEquipment.controls; }

  updateInvSubCategories() {
    //console.log("picture" + Global.documentName)
    console.log("category" + this.editEquipment.value.category)
    console.log("subCategory" + this.editEquipment.value.subCategory)
    if (Global.documentName != '') {
      console.log("if block is calling")
      let obj = {
        "loginData": this.proData,
        //"emailId": this.emailId,
        "Id": this.Id,
        "category": this.editEquipment.value.category,
        "subCategory": this.editEquipment.value.subCategory,
        //"inventoryImage": '' + Global.documentName,
        //"createdOn": new Date()
      }
      console.log("edit obj" + JSON.stringify(obj))
      this.http.post(Global.url + '/editEqSubcategories', obj)
        .subscribe(data => {
          var PartInfo = data.json();
          if (PartInfo.status == 200) {
            //console.log("Part Data" + JSON.stringify(PartInfo))
            this.toastr.success("Equipment Subcategory Updated successfully...");
            this.router.navigate(['/accountSettings/accountSettings/6/4'])
          } else {
            this.toastr.warning("Failed to Update")
          }
        }, error => {
          //console.log(error);// Error getting the data
        });
    }
    else {
      console.log("ELse block is calling")
      if (Global.documentName == '') {
        let obj = {
          "loginData": this.proData,
          //"emailId": this.emailId,
          "Id": this.Id,
          "category": this.editEquipment.value.category,
          "subCategory": this.editEquipment.value.subCategory,
          //"inventoryImage": '' + this.invImg,
          //"createdOn": new Date()
        }
        //console.log("edit obj" + JSON.stringify(obj.inventoryImage))
        this.http.post(Global.url + '/editEqSubcategories', obj)
          .subscribe(data => {
            var PartInfo = data.json();
            if (PartInfo.status == 200) {
              //console.log("Part Data" + JSON.stringify(PartInfo))
              this.toastr.success("Equipment Subcategory Updated successfully...");
              this.router.navigate(['/accountSettings/accountSettings/6'])
            } else {
              this.toastr.warning("Failed to Update")
            }
          }, error => {
            //console.log(error);// Error getting the data
          });

      }
    }
  }

  cancelEquipmentSubCat(){
    this.router.navigate(['/accountSettings/accountSettings/6'])
  } 
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list() {
    this.router.navigate(['/accountSettings/accountSettings/6'])
  }
}
