import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global'
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

declare var $: any;

@Component({
  providers: [Beyond],
  selector: 'app-edit-pro-staff-role',
  templateUrl: './edit-pro-staff-role.component.html',
  styleUrls: ['./edit-pro-staff-role.component.scss']
})
export class EditProStaffRoleComponent implements OnInit {
  editRole: FormGroup
  id: any;
  viewRoles: any;
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  loginData: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.id = this.route.snapshot.params['id'];
    console.log("id" + JSON.stringify(this.id))
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.emailId = this.loginData.emailId
    this.viewRole(this.id)
    this.editRole = this.formBuilder.group({
      roleName: ['', Validators.required],
      status: ['', Validators.required]
    })

  }

  viewRole(id) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewStaffRoles/' + id, options)
      .subscribe(data => {
        var viewRoleData = data.json();
        this.viewRoles = viewRoleData.response[0];
        this.editRole = this.formBuilder.group({
          roleCode: [this.viewRoles.id, Validators.required],
          roleName: [this.viewRoles.roleName, Validators.required],
          status: [this.viewRoles.status, Validators.required]
        })
        console.log("Roles Data" + JSON.stringify(this.viewRoles))
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }

  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.editRole.value.roleName))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.editRole.value.roleName == "") {
      this.toastr.warning("roleName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkStaffRoles/' + this.editRole.value.roleName)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

  confirm(){
    $("#statusConfirmation").modal("show");
  }

  submit() {
    let obj = {
      "id": this.id,
      "roleName": this.editRole.value.roleName,
      "status": this.editRole.value.status,
      "modifiedBy": this.emailId
    }
    console.log("object data in edit" + JSON.stringify(obj))
    this.http.post(Global.url + '/editStaffRoleService', obj)
      .subscribe(data => {
        var roleData = data.json();
        console.log("roleData" + JSON.stringify(roleData))
        this.toastr.success('Satff Role Updated Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/1'])
      }, error => {
        this.toastr.warning('Unable to Connect to Server')// Error getting the data
      });
  }

  cancel() {
    this.router.navigate(['/accountSettings/accountSettings/1'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
