import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-pro-staff-role',
  templateUrl: './add-pro-staff-role.component.html',
  styleUrls: ['./add-pro-staff-role.component.scss']
})
export class AddProStaffRoleComponent implements OnInit {
  addRole: FormGroup;
  loginData: any;
  submitted = false
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  proData: any;
  emailId: any;
  activeTab: Number = 0;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder
  ) { 
  }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))

    console.log("loginData" + JSON.stringify(this.loginData))
    this.proData = this.loginData.loginCode
    this.addRole = this.formBuilder.group({
      roleName: ['', Validators.required],
    })
  }
  get f() { return this.addRole.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.addRole.invalid) {
      console.log("In valid")
      return;
    }
    // alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.addRole.value))
    this.submit()
  }
  mailIdVerification() {
    if (!this.addRole.value.roleName) {
      this.toastr.warning("roleName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkStaffRoles/' + this.addRole.value.roleName)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  submit() {
    let obj = {
      "roleName": this.addRole.value.roleName,
      "proId": this.loginData.loginCode,
      "createdBy": this.loginData.emailId,
      "status": "ACTIVE"
    }
    this.http.post(Global.url + '/addStaffRoles', obj)
      .subscribe(data => {
        var rolesData = data.json();
        console.log("rolesData" + JSON.stringify(rolesData))
        this.toastr.success('Role Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/1'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }
  cancel() {
    this.router.navigate(['/accountSettings/accountSettings/1'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}
