import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-messages-alerts',
  templateUrl: './add-messages-alerts.component.html',
  styleUrls: ['./add-messages-alerts.component.scss']
})
export class AddMessagesAlertsComponent implements OnInit {
  addMessageAlertsForm: FormGroup;
  loginData: any;
  submitted = false
  disableSignIn: boolean = false;
  SourceCodeData: any;
  mailError: boolean;
  activeTab: Number = 0;
  proData: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.emailId = this.loginData.emailId
    this.addMessageAlertsForm = this.formBuilder.group({
      category: ['', Validators.required],
      alerts: ['', Validators.required],
      sendTo: ['', Validators.required],
    })
  }
  get f() { return this.addMessageAlertsForm.controls; }

  onSubmit() {
    this.submitted = true;
    // stop here if form is invalid
    if (this.addMessageAlertsForm.invalid) {
      console.log("In valid")
      return;
    }

  }

  mailIdVerification() {
    console.log("teja" + JSON.stringify(this.addMessageAlertsForm.value.equipmentItemType))
    console.log("teja123" + JSON.stringify(this.proData))
    if (this.addMessageAlertsForm.value.equipmentItemType == "") {
      this.toastr.warning("equipmentItemType Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addMessageAlertsForm.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkEquipmentItemType/' + this.addMessageAlertsForm.value.equipmentItemType + "/" + this.proData)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
  saveItemType() {
    let obj = {
      "proId": this.loginData.loginCode,
      "category": this.addMessageAlertsForm.value.category,
      "alerts": this.addMessageAlertsForm.value.alerts,
      "sendTo": this.addMessageAlertsForm.value.sendTo,
      "createdBy": this.emailId,
    }
    this.http.post(Global.url + '/addMessageAlerts', obj)
      .subscribe(data => {
        var messageData = data.json();
        console.log("addMessageAlerts" + JSON.stringify(messageData))
        this.toastr.success('Message & ALerts  Data Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/8'])
      }, error => {
        console.log(error);// Error getting the data
      });
    // this.router.navigate(['/proz/accountSettings'])
  }

  cancelItemType() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }

  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}

