import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http'
import { ToastrService } from 'ngx-toastr';
import { Beyond } from '../../../../../utilities/beyond';
import { Global } from '../../../../../global/global';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormControl, FormGroup, FormArray, Validators } from '@angular/forms';
@Component({
  providers: [Beyond],
  selector: 'app-edit-authorization-tnc',
  templateUrl: './edit-authorization-tnc.component.html',
  styleUrls: ['./edit-authorization-tnc.component.scss']
})
export class EditAuthorizationTncComponent implements OnInit {
  editAuthorizationTnc: FormGroup
  Id: any;
  loginData: any;
  proData: any;
  submitted: boolean;
  account: any;
  checkData: boolean;
  completionId: boolean = false;
  checkedValues: any;
  emailId: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    public toastr: ToastrService,
    public http: Http,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute, ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.Id = this.route.snapshot.params['id']
    console.log("id" + this.Id)
    this.viewdata()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode
    this.emailId = this.loginData.emailId
    this.editAuthorizationTnc = this.formBuilder.group({
      authorizationtostarTermConditions: ['', Validators.required],
      RequiredInvoceCompletion: ['', Validators.required],

    })

  }
  get f() { return this.editAuthorizationTnc.controls; }
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.editAuthorizationTnc.invalid) {
      return;
    }

  }
  requiredInvoiceCompletion(checkData) {
    if (checkData == '') {
      this.checkData == true
    }
    else {
      this.checkData = false
    }

  }
  viewdata() {
    console.log("employee" + this.Id)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewAuthorizationTNC/' + this.Id, options)
      .subscribe(data => {
        console.log('data' + data)
        var response = data.json();
        if (response.status == 200) {
          this.account = response.response[0];
          this.checkedValues = this.account.RequiredInvoceCompletion
          console.log("employee Data" + this.checkedValues)
          this.featchDetails(this.account)
        } else { }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
    this.checkedValues = Number(['RequiredInvoceCompletion']) === 0 ? false : true;
  }
  featchDetails(x) {
    this.editAuthorizationTnc = this.formBuilder.group({
      authorizationtostarTermConditions: [x.authorizationtostarTermConditions, Validators.required],
      RequiredInvoceCompletion: [x.RequiredInvoceCompletion, Validators.required],
    })
    console.log()
  }
  EditauthorizationtostarTermConditionss() {
    let obj = {
      "id": this.Id,
      "authorizationtostarTermConditions": this.editAuthorizationTnc.value.authorizationtostarTermConditions,
      "RequiredInvoceCompletion": this.editAuthorizationTnc.value.RequiredInvoceCompletion,
      "modifiedBy": this.emailId,
      "status": "ACTIVE"
    }
    console.log("RequiredInvoceCompletion value" + JSON.stringify(obj.RequiredInvoceCompletion))
    this.http.post(Global.url + '/editAuthorizationTNC', obj)
      .subscribe(data => {
        var groupData = data.json();
        console.log("groupData" + JSON.stringify(groupData))
        this.toastr.success('Group Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings'])
      }, error => {
        console.log(error);// Error getting the data
      });
  }

  cancelauthorizationtostarTermConditions() {
    this.router.navigate(['/accountSettings/accountSettings/5'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
}