import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { ActivatedRoute, Router } from '@angular/router';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';

@Component({
  providers: [Beyond],
  selector: 'app-add-pbsub-categories',
  templateUrl: './add-pbsub-categories.component.html',
  styleUrls: ['./add-pbsub-categories.component.scss']
})
export class AddPBSubCategoriesComponent implements OnInit {
  loginData: any;
  proData: any;
  emailId: any;
  addPBSubCategories: FormGroup;
  submitted: boolean;
  categoryDet: any;
  mailError: boolean;
  disableSignIn: boolean;
  mailResponse: any;


  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public toastr: ToastrService,
    private formBuilder: FormBuilder,
  ) { }
  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proData = this.loginData.loginCode,
      this.emailId = this.loginData.emailId,
      this.addPBSubCategories = this.formBuilder.group({
        category: ["", Validators.required],
        subCategory: ["", Validators.required],
        picture: ['', Validators.required],
      })
    this.listCategories()
  }
  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.addPBSubCategories.invalid) {
      this.saveINVItemType()
      return;
    }
  

  }

  get f() { return this.addPBSubCategories.controls; }
  saveINVItemType() {
    console.log(this.addPBSubCategories.value.category)
    console.log(this.addPBSubCategories.value.subCategory)
    if (this.addPBSubCategories.value.category != '' && this.addPBSubCategories.value.subCategory != '' &&  this.mailResponse.status == "FALSE"){
    let obj = {
      "proId": this.proData,
      "emailId": this.emailId,
      "category": this.addPBSubCategories.value.category,
      "subCategory": this.addPBSubCategories.value.subCategory,
      "createdOn": new Date(),
      "createdBy": this.emailId
    }
    console.log("this is obj" + JSON.stringify(obj))
    this.http.post(Global.url + '/addPBSubCategory', obj)
      .subscribe(data => {
        var rolesData = data.json();
        console.log("rolesData" + JSON.stringify(rolesData))
        this.toastr.success('Pricebook SubCategory Added Successfuly...');
        this.router.navigate(['/accountSettings/accountSettings/4/3'])
      }, error => {
        console.log(error);// Error getting the data
      });
    }
  }


  cancelINVItemType() {
    this.router.navigate(['/accountSettings/accountSettings/4/3'])
  }
  inventory() {
    this.router.navigate(['/accountSettings/accountSettings/4/6'])
  }
  accountsettings() {
    this.router.navigate(['/accountSettings/accountSettings'])
  }
  list() {
    this.router.navigate(['/accountSettings/accountSettings/4/6'])
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  listCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/groupProCategory/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.categoryDet = response.response;
          console.log("category" + JSON.stringify(this.categoryDet))
        } else {
          this.toastr.warning("No Records Found")
        }
      }, error => {
        this.toastr.warning("Unable to connect to server")
      })
  }

  subCategoryChecking() {
    console.log("This is the subCategory:" + JSON.stringify(this.addPBSubCategories.value.subCategory))
    if (this.addPBSubCategories.value.subCategory == "") {
      this.toastr.warning("subCategory is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkPBSubCategory/' + this.addPBSubCategories.value.subCategory + "/" + this.loginData.loginCode)
        .subscribe(data => {
          this.mailResponse = data.json();
          if (this.mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

}
